import React from 'react'
import styled from 'styled-components';
// import { useLocation, useNavigate } from 'react-router-dom';
// import { Link as LinkScroll } from 'react-scroll';
import { useApp } from '../../context/Context';
import textContext from '../../textContext';
import { medium, twitter, discord } from '../../assets/images';

const FooterWrapper = styled('footer')`
	position: absolute;
	width: 100%;
	bottom: 0;
	left: 0;
	height: 10vh;
	z-index: 1;
	&:before {
		z-index: -1;
		background-color: #121122;
		opacity: .7;
		position: absolute;
		content: '';
		height: 100%;
		width: 100%;
	}
`;

export default function Footer() {

	const { language } = useApp();
	const text = textContext[language].footer;

	// let location = useLocation();
	// let navigate = useNavigate();

	// const handleHome = () => {
	// if (location.pathname !== '/') {
	//   navigate("/");
	// }
	// }

	const icons = [
		{ icon: medium, link: 'https://medium.com/@aperturefinance' },
		{ icon: discord, link: 'https://discord.gg/MGHguks25G' },
		{ icon: twitter, link: 'https://twitter.com/ApertureFinance' },
	]

	return (
		<FooterWrapper>
			<div className="container">
				<div className="footerContainer">
					{/*					<LinkScroll spy={true} smooth={true} to="home" onClick={handleHome} style={{margin:0}}>
							<img src={logo} alt="aperture finance logo" />
						</LinkScroll>
*/}
					<div className="linkContainer">
						{
							text.listLeft.map((l, i) => (
								<span key={i} className="footerChild">
									<a target="_blank" rel="noreferrer" href={`/${l.link}`}>
										<p>{l.name}</p>
									</a>
									{i + 1 !== text.listLeft.length && <hr />}
								</span>
							))
						}
					</div>
					<div className="iconContainer">
						{
							icons.map((l, i) => (
								<a target="_blank" rel="noreferrer" key={i} href={l.link}>
									<img src={l.icon} alt={l.icon} />
								</a>
							))
						}
					</div>
					<div className="linkContainer">
						{
							text.listRight.map((l, i) => (
								l.link ? (
									<span key={i} className="footerChild">
										<a target="_blank" rel="noreferrer" href={`/${l.link}`}>
											<p>{l.name}</p>
										</a>
										{i + 1 !== text.listRight.length && <hr />}
									</span>
								) : (
									l.a ? (
										<span key={i} className="footerChild">
											<a href="mailto:contact@aperture.finance">
												<p>{l.name}</p>
											</a>
											{i + 1 !== text.listRight.length && <hr />}
										</span>
									) : (
										<span key={i} className="footerChild">
											<p style={{ alignSelf: 'center' }}>{l.name}</p>
										</span>
									))
							))

						}
					</div>
				</div>
			</div>
		</FooterWrapper>
	)
}