import { createGlobalStyle } from "styled-components";
import { oval } from "../assets/images";

const GlobalStyle = createGlobalStyle`

/*----------------------------
	xs: 0px
	sm: 600px
	md: 900px
	lg: 1200px
	xl: 1536px
----------------------------*/

/*----------------------------
	Global styles
----------------------------*/

// Layout
	.container {
		padding: 0 7%;
	}
	.textContainer {
		width: 1075px;
		margin: auto;
		@media (max-width: 1075px) {
			width: 100%;
		}
	}
	.padding {
		padding: 4.25rem 0;
		@media (max-width: 600px) {
    	padding: 3rem 0;
		}
	}
	.linkClass {
		color: white!important;
		text-decoration: none!important;
	}
	.colorPrimary {
		color: #6D4AFF;
	}
	.colorSecondary {
		color: #00ACED;
	}
	.textHeader {
		text-transform: capitalize;
		text-align: center;
		font-size: 46px;
		line-height: 56px;
		font-weight: bold;
    margin: auto;
    padding: 1rem 0 2rem;
    width: fit-content;
    @media (max-width: 1200px) {
			font-size: 30px;
			line-height: 38px;
    }
    @media (max-width: 900px) {
			font-size: 24px;
			line-height: 20px;
    }
	}
	.subHeader {
		text-transform: capitalize;
		text-align: center;
		font-size: 24px;
		line-height: 39px;
		font-weight: bold;
		margin: 0;
		padding: 0;
    @media (max-width: 1200px) {
			font-size: 22px;
			line-height: 33px;
    }
    @media (max-width: 900px) {
			font-size: 20px;
			line-height: 28px;
    }
	}
	.MuiDrawer-paper {
		background-color: rgba(0,0,0,.75)!important;
	}
	.imgSort {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		justify-content: space-between;
		@media (max-width: 1200px) {
			margin: 0!important;
			flex-direction: column;
		}
	}

	.gradientClass {
    background: linear-gradient(248.54deg, #45D7D9 10.13%, #8383FF 67.36%, #976DFF 99.98%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;
    width: fit-content;
	}

// Navbar
	nav {
		position: fixed;
		width: 100%;
		top: 0;
		left: 0;
		z-index: 10;
		&#mobileNav {
			position: sticky;
			height: 55px;
			background: #121122;
			& > div {
				height: 100%;
				display: flex;
				align-items: center;
				justify-content: space-between;
			}
			& img {
		    top: 5px;
		    position: relative;
				height: auto;
				width: 100px;
			}
		}
	}
	#navbar {
		display: flex;
		height: 80px;
		background-color: rgba(18, 17, 34, .7);
		img {
			position: absolute;
			top: 26px;
			height: auto;
		}
		.linkContainer {
			display: flex;
			align-items: center;
			margin: auto;
			margin-right: 0;
			.linkClass {
				cursor: pointer;
				text-transform: capitalize;
				margin: 0 1.1rem 0;
				font-size: 14px;
				&:hover {
					color: #6D4AFF!important;
				}
			}
		}
		.navButton {
			margin: auto 0;
			margin-left: 3rem;
		}
		#languageButton {
			margin-left: auto;
			cursor: pointer;
			display: flex;
			align-items: center;
			p {
				margin: 0;
				margin-left: .25rem;
				font-size: 14px;
				text-transform: capitalize;
			}
		}
		@media (max-width: 1200px) {
			.linkContainer {
				.linkClass {
					font-size: 12px;
					margin: 0 .5rem 0;
				}
			}
		}
	}
	#basic-menu {
		li {
			text-transform: capitalize;
			font-size: 14px!important;
		}
	}

// Home
	#home {
		display: flex;
		align-items: center;
		@media (max-width: 900px) {
			flex-direction: column;
		}
		.container {
			position: absolute;
			top: 33%;
			left: 0;
			z-index: 10;
			max-width: 625px;
			.appText {
				button {
					font-weight: 400;
					min-height: 52px;
					p {
						font-size: 20px!important;
					}
				}
			}
	    @media (min-width: 1600px) {
				max-width: 45vw;
	    }
			@media (max-width: 900px) {
				.appText {
					text-align: center;
					button {
						min-height: 44px;
						p {
							font-size: 16px!important;
						}
					}
				}
				position: relative;
				top: 0;
			}
		}
		h5 {
			margin: 0;
			font-weight: 500;
			font-size: 24px;
			line-height: 34px;
		}
		h1 {
			margin: 16px 0;
			font-weight: 700;
			font-size: 48px;
			line-height: 56px;
	    @media (min-width: 1600px) {
				font-size: 3.5vw;
	    	line-height: 4vw;
	    }
			@media (max-width: 1100px) {
				font-size: 40px;
				line-height: 50px;
			}
			@media (max-width: 900px) {
				font-weight: bold;
				font-size: 24px;
				line-height: 30px;
				text-align: center;
			}
		}
		h6 {
			margin: 0;
			max-width: 450px!important;
			font-weight: normal;
			font-size: 20px;
			line-height: 33px;
			white-space: break-spaces;
			@media (min-width: 1600px) {
			  font-size: 24px;
			}
			@media (max-width: 1100px) {
		    font-size: 16px;
		    line-height: 24px;
			}
			@media (max-width: 900px) {
				font-size: 14px;
				line-height: 22px;
				text-align: center;
			}
		}
		button {
			margin-top: 2.5rem;
		}
	}

// Value
	#value {
		position: relative;
		z-index: 1;
	}
	.celestialImg {
		text-align: center;
		margin: auto;
		position: relative;
		img {
			position: relative;
			z-index: 1;
			top: 0;
			left: 0;
			right: 0;
			right: 0;
			margin: auto;
			width: 50vw;
			height: auto;
			max-width: 1125px;
		}
		&:before {
			display: block;
			position: absolute;
			content: '';
			height: 100%;
			width: 100%;
			background-image: url(${oval});
			background-size: 23vw;
	    background-repeat: no-repeat;
	    background-position: center;
	    top: -14.5px;
	    left: -7px;
	    z-index: -5;
	    animation-name: celestial-animate;
			animation-duration: 20s;
			animation-timing-function: linear;
			animation-iteration-count: infinite;
			@media (min-width: 2225px) {
				background-size: 500px;
				top: -14px;
				left: 13px;
			}
		}
	}
	#value .textContainer {
		.gridItem {
			width: 290px!important;
			position: absolute;
			white-space: pre-line;
			z-index: 1;
			top: 0;
			img {
				margin-top: 1.5rem; 
			}
			h3 {
				font-size: 18px;
				margin-bottom: 0rem;
    		line-height: 28px;
			}
			ul {
				list-style: none;
				padding: 0;
			}
			li {
				font-size: 13.5px;
    		line-height: 22px;
    		color: #B0B5C4;
    		margin-bottom: .5rem;
			}
			@media (max-width: 900px) {
				position: relative;
				left: 0!important;
				top: 0!important;
				text-align: center;
		    margin: auto;
			}
		}
		.MuiGrid-root {
			&:nth-child(even) {
				@media (min-width: 900px) {
					.gridItem {
						margin-left: auto;
					}
				}
			}
		}
		.value1 {
			top: 20%;
			left: -2.5%;
		}
		.value2 {
			top: 20%;
			right: -5%;
		}
		.value3 {
			top: 55%;
			left: -2.5%;
		}
		.value4 {
			top: 55%;
			right: -5%;
		}
		@media (max-width: 1200px) {
			.value1, .value3 {
				left: 5%;
			}
			.value2, .value4 {
				left: 7.5%;
				right: 2.5%;
			}
			.value1, .value2 {
				top: 15%;
			}
			.value3, .value4 {
				top: 50%;
			}
		}
		@media (min-width: 2049px) {
			.value1, .value3 {
				left: -7.5%;
			}
			.value2, .value4 {
				right: -12.5%;
			}
		}
	}

// Strategy
	#strategy {
		max-width: 1250px;
		margin: auto;
		.cardContainer:first-of-type {
			justify-content: center;
			@media (max-width: 600px) {
				padding: 0.5rem;
			}
		}
		.cardContainer:last-of-type {
			justify-content: center;
			.strategyCard {
				margin: 1rem 10.5px;
			}
			@media (max-width: 600px) {
				justify-content: space-between;
			}
		}
	}
	.cardContainer {
		display: flex;
		margin-top: 1rem;
		flex-wrap: wrap;
		.strategyCard:nth-child(odd) {
			&:before {
				background: linear-gradient(359.97deg, #32C5FF 0.03%, #8DD1FB 52.23%, #E4A677 99.97%);
			}
		}
		.strategyCard:nth-child(even) {
			&:before {
				background: linear-gradient(180deg, #7FFBCE -14.2%, #A17DFF 38.64%, #672CFF 106.19%);
			}
		}
		@media (max-width: 990px) {
    	justify-content: center!important;
		}
		@media (max-width: 900px) {
    	margin-bottom: 2rem;
		}
	}
	.strategyCard {
		cursor: pointer;
		height: 117.5px;
		min-width: 230px;
		max-width: 230px;
		background-color: #23262F;
		border-radius: 9px;
		margin: 1rem 8px;
		position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
		img {
			height: 22.5px;
			width: 22.5px;
			@media (max-width: 900px) {
				height: 15px;
				width: 15px;
			}
		}
		span {
			padding: 1rem;
			display: flex;
			align-items: center;
			@media (max-width: 900px) {
				padding: .5rem;
			}
		}
		span:first-of-type {
			padding-right: 0;
			padding-bottom: 0;
			p {
				margin: 0;
				padding: 0;
				margin-left: .75rem;
				color: #B0B5C4;
				font-size: 14px;
				line-height: 19.5px;
				@media (max-width: 900px) {
					font-size: 10px;
					font-weight: 600;
					margin-left: .5rem;
					line-height: 14px;
				}
			}
		}
		span:last-of-type {
			padding-bottom: .5rem;
			justify-content: space-between;
			.apy {
				height: 25px;
				width: 55px;
				background: #353945;
				text-align: center;
				border-radius: 12.5px;
				@media (max-width: 900px) {
					width: 40px;
					height: 18px;
					font-size: 12px;
				}
			}
			p {
				font-size: 20px;
				font-weight: bold;
				line-height: 33px;
				margin: 0;
				padding: 0;
				@media (max-width: 900px) {
					font-size: 14px;
				}
			}
		}
		@media (max-width: 900px) {
			min-width: auto;
			width: 162px;
			height: 82px;
		}
	}
	.strategyCard:before {
		content: '';
		z-index: -10;
		position: absolute;
		top: -1px;
		left: -1px;
		height: calc(100% + 3px);
		width: calc(100% + 2px);
		display: block;
		border-radius: 9px;
	}

// How
	#how {
		text-align: center;
		.textContainer > div {
			position: relative;
		}
		.textContainer {
			position: relative;
			& > img {
				width: 50px;
				position: absolute;
				top: 700px;
				opacity: 0;
			}
		}
		.howContainer {
			 img {
				margin-top: 2rem;
				width: 840px;
				height: auto;
			}
			@media (max-width: 1150px) {
				img {
					width: 500px;
				}
			}
			@media (max-width: 900px) {
		    display: flex;
		    flex-direction: column-reverse;
		    img {
			    width: 90%;
			    margin: 2rem auto;
		    }
		    & > div {
					display: flex;
					flex-direction: column;
					margin: auto;
		    }
			}
		}
		.howClass {
			display: flex;
			align-items: flex-start;
			position: absolute;
			.item {
				background: #6D4AFF;
				border: 1px solid #000000;
				height: 28px;
				width: 28px;
				border-radius: 50%;
				display: flex;
				align-items: center;
				justify-content: center;
				margin: 0 .5rem;
				position: relative;
				&:before {
					position: absolute;
					content: '';
					top: -1px;
					left: -5px;
					display: block;
					height: 28px;
					width: 28px;
					background: #3F35EF;
					border: 1px solid #000000;
					z-index: -1;
					border-radius: 50%;
				}
				@media (max-width: 600px) {
					height: 25px;
					width: 25px;
					&:before {
						height: 25px;
						width: 25px;
					}
				}
			}
			.value {
				font-size: 14px;
				line-height: 22px;
				text-align: left;
				width: 200px;
				white-space: break-spaces;
				@media (max-width: 1150px) {
					font-size: 11px;
				}
				@media (max-width: 900px) {
					width: 290px;
					font-size: 15px;
					margin-bottom: 2rem;
					padding-left: .5rem;
				}
			}
			&.how1 {
				top: 400px;
			}
			&.how2 {
				right: -25px;
				top: 200px;
			}
			&.how3 {
				right: -25px;
				top: 350px;
			}
			&.how4 {
				top: 600px;
			}
			@media (max-width: 1150px) {
				&.how1 {
					top: 225px;
					left: 7.5%;
				}
				&.how2 {
					top: 150px;
					right: 4%;
				}
				&.how3 {
					top: 240px;
					right: 7.5%;
				}
				&.how4 {
					top: 400px;
					left: 11.5%;
				}	
			}
			@media (max-width: 900px) {
		    position: relative;
		    top: 0px!important;
		    right: 0px!important;
		    left: 0px!important;
			}
		}
	}

// Offering
	#offering {
		.offeringClass {
			background-color: #212030;
			padding: 2rem;
			margin: auto;
			margin-bottom: 2rem;
			border-radius: 10px;
			overflow: hidden;
			h2 {
				font-size: 30px;
				line-height: 49px;
				font-weight: bold;
				text-transform: capitalize;
				margin: 0;
				padding: 0;
			}
			p {
				font-size: 16px;
				line-height: 26px;
			}
			.grid {
				img {
					width: 502px;
				}
				&:first-of-type {
					padding-right: 1rem;
				}
				&:last-of-type {
					margin-left: auto;
				}
			}
			@media (max-width: 1100px) {
				width: 97.5%;
				p {
					font-size: 14px;
				}
			}
			@media (max-width: 900px) {
				background-color: transparent;
				padding: 1rem;
				margin-bottom: 1rem;
				border-radius: 0px;
				h2 {
					font-size: 20px;
					line-height: 22px;
					text-align: center;
				}
				p {
					font-size: 14px;
					line-height: 26px;
					text-align: center;
				}
				.grid {
					img {
						margin-top: 1.5rem;
						background-color: #212030;
						border-radius: 10px;
						width: 100%;
					}
				}
			}
		}
	}

// Token
	#token {
		.textContainer {
			position: relative;
		}
		.content {
			text-align: center;
			p {
				margin: auto;
				margin-bottom: 3rem;
				font-size: 20px;
				line-height: 33px;
				max-width: 925px;
				@media (max-width: 1200px) {
					font-size: 16px;
					line-height: 26px;
					padding: 0 3rem;
				}
				@media (max-width: 900px) {
					font-size: 12px;
					line-height: 20px;
					padding: 0 1.5rem;
					margin-bottom: 0;
				}
			}
			h2 > span {
				font-size: 28px;
				margin: auto .5rem;
			}
		}
		.tokenList {
			display: flex;
			align-items: center;
			justify-content: space-around;
			width: 100%;
			height: 100%;
			padding-top: 6rem;
			flex-wrap: wrap;
			.body {
				& > div {
					padding: 1rem .75rem;
					padding-bottom: 0;
					p:first-of-type {
						padding: 0;
						margin: 0;
						font-weight: bold;
						font-size: 18px;
						@media (max-width: 900px) {
							font-size: 15px;
							line-height: 14px;
						}
					}
					p:last-of-type {
						margin-top: .75rem;
						@media (max-width: 900px) {
							font-size: 12px;
							line-height: 16px;
						}
					}
				}
			}
			@media (max-width: 1200px) {
				justify-content: center;
			}
			@media (max-width: 900px) {
		    padding-top: 3rem;	
			}
		}
		@media (max-width: 1200px) {
			margin-bottom: 0rem;
		}
	}

// Stats
	#stats {
		background-color: #1f1f2f;
		text-align: center;
		h1 {
			font-size: 34px;
			font-weight: bold;
			color: #32c4fe;
		}
		p {
			padding: 0 2rem;
			font-size: 16px;
		}
	}

// Team
	#about {
		padding: 2rem 0 0;
		.teamContainer {
			margin-top: 2rem;
			display: flex;
			justify-content: space-evenly;
			flex-wrap: wrap;
		}
		.teamClass {
			width: 333px!important;
			display: flex;
			align-items: center;
			flex-direction: column;
			text-align: center;
			width: 100%;
			& > img {
				width: 115px;
		    object-fit: cover;
		    border-radius: 50%;
			}
			a {
				display: flex;
				align-items: center;
				text-decoration: none;
				p {
					font-size: 20px;
					font-weight: 400;
					opacity: 1;
					padding: 0;
				}
				img {
					margin-left: .5rem;
				 	width: 32px;
				 	height: 32px;
				}
			}
			p {
				font-weight: normal;
				font-size: 18px;
				line-height: 30px;
				text-align: center;
				letter-spacing: -0.375px;
				color: #FFFFFF;
				opacity: 0.6;
			}
		}
		@media (max-width: 1200px) {
			.teamClass {
				p {
					font-size: 16px;
					line-height: 25px;
				}
			}
		}
		@media (max-width: 900px) {
			.teamContainer {
	    	justify-content: center;
	    }
			.teamClass {
				margin-bottom: 2rem;
				& > img {
					width: 85px;
				}
				p {
					font-size: 14px;
					padding: 0 2rem;
					line-height: 20px;
				}
			}
		}
	}

// Featured
	#featured {
		.textContainer {
			width: 1200px;
			@media (max-width: 1200px) {
				width: 1075px;
			}
			@media (max-width: 1075px) {
				width: 100%;
			}
		}
	}

// Partners
	#partners {
		.textContainer {
			width: 1200px;
			@media (max-width: 1200px) {
				width: 1075px;
			}
			@media (max-width: 1075px) {
				width: 100%;
			}
		}
		.sponsorClass {
			margin-top: 2rem;
			img {
				padding: 1rem;
				height: auto;
				text-align: center;
				object-fit: contain;
			}
			p {
				padding: 1rem;
				margin: 0;
				width: 175px;
				font-size: 30px;
				font-weight: 700;
				letter-spacing: -1px;
				text-align: center;
				&.textSmall {
					font-size: 1.25rem;
				}
			}
		}
		@media (max-width: 600px) {
			.sponsorClass {
				& > div {
					height: 75px;
				}
				img {
					width: 105px;
					height: auto;
				}
				p {
					width: 105px;
					font-size: 18px;
					&.textSmall {
						font-size: .85rem;
					}
				}
			}
		}
	}

// Footer
	footer {
		& .container > div {
			display: flex;
			align-items: center;
			width: 100%;
			margin-top: 1rem;
			justify-content: space-between;
		}
		.footerContainer {
			margin: auto;
			& .iconContainer, & .linkContainer {
				min-width: 30%;
			}
			& .iconContainer {
				text-align: center;
			}
		}
		img {
			height: auto;
			width: 106px;
		}
		.linkContainer {
			display: flex;
		}
		.footerChild {
			margin: auto .5rem;
			display: flex;
			height: 100%;
			& hr {
		    margin: auto;
		    margin-left: 1rem;
		    height: 16px;
		    border: 0;
		    border-left: 1px solid white;
		    opacity: .1;
			}
		}
		a {
			color: white;
			text-decoration: none;
			text-transform: capitalize;
			font-size: 12px;
		}
		p {
			text-transform: capitalize;
			font-size: 12px;
		}
		.iconContainer {
			img {
				width: 30px;
			}
			a {
				margin: 0;
				margin-right: 1.25rem;
			}
			a:last-of-type {
				margin-right: 0;
			}
		}
		@media (max-width: 1150px) {
			.footerContainer {
				& .iconContainer, & .linkContainer {
					min-width: auto;
				}
			}
		}
		@media (max-width: 900px) {
			padding-bottom: 1rem;
			height: auto!important;
			.container {
				padding: 0;
				& > div {
					display: flex;
					flex-direction: column;
					a {
						margin: 0.5rem;
					}
				}
			}
			.footerChild {
				margin: auto 0.5rem auto 0;
				& hr {
					margin-left: .5rem;
				}
			}
		}
		@media (max-width: 600px) {
			.container {
				& > div {
					p, a {
						font-size: 10px;
					}
				}
			}	
		}
	}

// Notified
	#notified {
		.ml-field-group {
			position: relative;
			padding: .1rem;
			&:before {
				content: '';
				position: absolute;
				height: 102.5%;
				width: 100.5%;
				top: -.5px;
				left: -1px;
				border-radius: 6px;
				background: linear-gradient(359.97deg,#32C5FF 0.03%,#8DD1FB 52.23%,#E4A677 99.97%);
			}
			input{
				position: relative;
				border: 0!important;
			}
		}
		@media (max-width: 900px) {
			margin-bottom: 5rem;
			.inputClass {
				width: 350px;
				input {
					width: 300px;
				}
			}
		}
	}

// Terms
	#terms {
		margin: 5% auto auto;
		@media (max-width: 600px) {
			margin-bottom: 15%;
		}
	}

// Privacy
	#privacy {
		margin: 5% auto auto;
		@media (max-width: 600px) {
			margin-bottom: 15%;
		}
	}

// Mediakit
	#mediakit {
		margin: 5% auto auto;
		padding: 0 2rem;
		.logoColorClass {
			margin-top: 2rem;
			display: flex;
			justify-content: space-between;
			width: 100%;
			& > div {
				width: 30%;
			}
			span {
				display: flex;
				height: auto;
				margin-top: .75rem;
				cursor: pointer;
				width: max-content;
				img {
					height: 100%;
				}
				p {
					margin: 0;
					padding: 0;
					margin-left: .5rem;
				}
			}
		}
		.colorClass {
			height: 140px;
			width: 100%;
		}
		@media (max-width: 750px) {
			.logoColorClass {
				display: block;
				& > div {
					width: 100%;
					margin-bottom: 2rem;
				}
			}
			.colorClass {
				height: 200px;
			}
		}
		@media (max-width: 600px) {
			margin-bottom: 15%;
		}
	}
`;

export default GlobalStyle;
